*,
*:before,
*:after {
  box-sizing: border-box;
}

/* Regular */

@font-face {
  font-family: 'League Gothic';
  src: url("../fonts/leaguegothic-regular-webfont.eot");
  src: url("../fonts/leaguegothic-regular-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/leaguegothic-regular-webfont.woff") format("woff"), url("../fonts/leaguegothic-regular-webfont.ttf") format("truetype"), url("../fonts/leaguegothic-regular-webfont.svg#league_gothic_italicregular") format("svg");
  font-weight: 400;
  font-style: normal;
}

body {
  margin: 0 auto;
  background-color: #ffffff;
  font-family: Arial, sans-serif;
  font-weight: 400;
  font-style: normal;
}

.seconadary-color {
  /* change letter-color to light-blue in text*/
  color: #22aaec;
}

.header {
  display: block;
  width: 100%;
  border-bottom: 3px solid #22aaec;
}

.header__navigation {
  display: block;
}

.header__name {
  display: block;
  width: 100%;
  margin: 0 auto;
  padding-top: 9px;
  color: #076e9f;
  font-family: "League Gothic", sans-serif;
  font-size: 32px;
  text-align: center;
  text-transform: uppercase;
}

.header__social-links {
  display: block;
  position: absolute;
  margin: 0;
  top: 16px;
  right: 30px;
}

.header__link-container {
  display: inline-block;
  padding: 3px 0;
  border: 1px solid transparent;
}

.header__link-container.-hover {
  border-radius: 0 0 8px 8px;
}

.header__link-container.-hover:hover {
  transition: 0.7s;
  border-top: 1px solid #22d5ff;
  background-color: rgba(36, 0, 0, 0.79);
}

.header__link-container a {
  display: block;
  background: url("../assets/headerLinks.png");
  width: 18px;
  height: 18px;
}

.header__link-container.-evernote {
  background-position: 0 0;
}

.header__link-container.-twitter {
  background-position: -23px 0;
}

.header__link-container.-pinterest {
  background-position: -46px 0;
}

.header__link-container.-linkedin {
  background-position: -69px 0;
}

.header__link-container.-facebook {
  background-position: -92px 0;
}

.header__link-container.-delicious {
  background-position: -115px 0;
}

.header__navigation-container {
  display: block;
  width: 100%;
  margin: 0 auto;
  padding-top: 18px;
  padding-bottom: 12px;
  text-align: center;
}

.header__navigation-link {
  display: inline-block;
  border: 1px solid transparent;
  margin-right: 30px;
  padding: 5px 16px 4px;
}

.header__navigation-link.-active {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  border: 1px solid #045279;
  background: #076e9f linear-gradient(to top, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
}

.header__navigation-link.-active a {
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.55);
  color: #ffffff;
}

.header__navigation-link a {
  color: #707070;
  text-decoration: none;
  text-transform: uppercase;
}

.header__navigation-link:hover {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  border: 1px solid #045279;
  background: #076e9f linear-gradient(to top, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
}

.header__navigation-link:hover a {
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.55);
  color: #ffffff;
  transition: 0.7s;
}

.header__burger {
  display: none;
  position: absolute;
  left: 20px;
  top: 15px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  color: transparent;
  font-size: 0;
  background: url("../assets/menu.png") 100%, no-repeat;
  z-index: 1;
}

.header__checkbox {
  display: none;
}

.header__checkbox:checked + .header__burger {
  background-image: url("../assets/close.png");
}

.header__checkbox:checked ~ .header__navigation-container {
  display: block;
}

@media all and (max-width: 599px) {
  .header__name {
    padding-bottom: 40px;
  }

  .header__navigation-link {
    display: block;
    width: 120px;
    transition: 1s;
    margin: 0 auto;
  }

  .header__navigation-link.-active {
    background: transparent;
    border: 1px solid transparent;
  }

  .header__navigation-link.-active a {
    color: #707070;
    text-shadow: none;
  }

  .header__navigation-link.-active:hover {
    border: 1px solid #045279;
    background: #076e9f linear-gradient(to top, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 0.1) 100%);
  }

  .header__navigation-link.-active:hover a {
    text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.55);
    color: #ffffff;
    transition: 0.7s;
  }

  .header__burger {
    display: block;
    top: 55px;
  }

  .header__navigation-container {
    display: none;
  }

  .header__social-links {
    top: 56px;
  }
}

.main-banner {
  display: block;
  width: 100%;
  background: url("../assets/main-bg.png") 100%, no-repeat;
  background-size: cover;
  padding-top: 93px;
  margin: 0 auto;
}

.main-banner__visit {
  display: block;
  max-width: 284px;
  color: #076e9f;
  font-family: "League Gothic", sans-serif;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 4px 12px 3px;
  margin: 0 auto;
}

.main-banner__introduce-container {
  margin: 19px 0 76px;
}

.main-banner__introduce {
  display: block;
  max-width: 800px;
  color: #076e9f;
  font-family: "League Gothic", sans-serif;
  font-size: 30px;
  text-align: center;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 7px 0 6px;
  margin: 0 auto;
}

.main-banner__comments-container {
  height: 122px !important;
  padding-top: 26px;
  padding-bottom: 26px;
  background-color: rgba(0, 0, 0, 0.5);
}

.main-banner__slider-container {
  display: block;
  width: 25%;
}

.main-banner__slider-item {
  display: block;
  width: 190px !important;
  height: 69px;
  margin: 0 auto;
  border-radius: 35px 0 0 34px;
  background-color: rgba(255, 255, 255, 0.9);
  outline: none;
}

.main-banner__people-photo {
  display: block;
  float: left;
  margin-left: -1px;
}

.main-banner__people-opinion {
  display: block;
  color: #515151;
  font-size: 9px;
  font-style: italic;
  text-align: left;
  white-space: pre;
  padding-top: 10px;
  padding-left: 86px;
}

.main-banner__p-s {
  display: block;
  color: #828282;
  font-size: 9px;
  text-align: left;
  padding-left: 86px;
}

@media all and (max-width: 1170px) {
  .main-banner__slider-container {
    width: 33%;
  }
}

@media all and (max-width: 715px) {
  .main-banner__slider-container {
    width: 50%;
  }
}

@media all and (max-width: 477px) {
  .main-banner__slider-container {
    width: 100%;
  }
}

/*slick-slider-stylesheets*/

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  width: 190px;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
  /*width: 190px;     !*custom style*!*/
  /*margin: 0 25px;   !*custom style*!*/
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

@charset "UTF-8";

/* Slider */

.slick-loading .slick-list {
  background: #fff;
}

/* Icons */

@font-face {
  font-family: 'slick';
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/slick-fonts/slick.eot");
  src: url("../fonts/slick-fonts/slick.eot?#iefix") format('embedded-opentype'), url("../fonts/slick-fonts/slick.woff") format('woff'), url("../fonts/slick-fonts/slick.ttf") format('truetype'), url("../fonts/slick-fonts/slick.svg#slick") format('svg');
}

/* Arrows */

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background: transparent;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: .25;
}

.slick-prev:before,
.slick-next:before {
  font-family: 'slick';
  font-size: 20px;
  line-height: 1;
  opacity: .75;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto;
}

.slick-prev:before {
  content: '←';
}

[dir='rtl'] .slick-prev:before {
  content: '→';
}

.slick-next {
  right: -25px;
}

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px;
}

.slick-next:before {
  content: '→';
}

[dir='rtl'] .slick-next:before {
  content: '←';
}

/* Dots */

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: '•';
  text-align: center;
  opacity: .25;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  opacity: .75;
  color: black;
}